import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Http } from '@angular/http';
import { AppConfig } from '../../../../app/app.config';

/**
 * Generated class for the StudyplayPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-studyplay',
  templateUrl: 'studyplay.html',
})
export class StudyplayPage {
  article: any = this.navParams.data.article;
  enterTime = 0;
  points;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    private http: Http) {
  }

  ionViewDidLoad() {
    this.enterTime = new Date().getTime();
    console.log('ionViewDidLoad StudyplayPage');
  }
  ionViewWillLeave() {
    let duration = ((new Date().getTime() - this.enterTime) / 1000 / 60 / 45);

    this.points = Number.parseFloat(localStorage.getItem("xfwPoints")) || 0;
    this.points += duration;
    this.points=this.points.toFixed(5);
    this.updatePoints(this.points);
  }
  updatePoints(points) {
    this.http.post(AppConfig.getUrl() + "/user/xfwPoints",
      null,{params: {
        userId: localStorage.getItem("userid"),
        points: points
      }}).subscribe(r => {
        let j = r.json();
        if (j.status) {
          localStorage.setItem("xfwPoints",this.points+"");
        }
      }, e => {

      });
  }

}



